// pure functions - —Ñ—É–Ω–∫—Ü–∏–∏ –∫–æ—Ç–æ—Ä—ã–µ –∑–∞–≤–∏—Å—è—Ç —Ç–æ–ª—å–∫–æ –æ—Ç –∞—Ä–≥—É–º–µ–Ω—Ç–æ–≤
// –Ω–µ –∑–∞–≤–∏—Å—è—Ç –æ—Ç –≥–ª–æ–±–∞–ª—å–Ω–æ–≥–æ –∫–æ–Ω—Ç–µ–∫—Å—Ç–∞
import { row, col } from './utils.js'

// —Ñ—É–Ω–∫—Ü–∏–∏ –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ html –¥–ª—è —Ä–∞–∑–ª–∏—á–Ω—ã—Ö –±–ª–æ–∫–æ–≤

// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ó–∞–≥–æ–ª–æ–≤–∫–∞
function title(block) {
	const tag = block.options.tag
	const styles = block.options.styles
	return row(col(`<${tag}>${block.value}</${tag}>`))
}

// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ø–∞—Ä–∞–≥—Ä–∞—Ñ–∞
function text(block) {
	return row(col(`<p>
			${block.value}
		</p>`))
}

// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –∫–æ–ª–æ–Ω–æ–∫
function columns(block) {
	const html = block.value.map(col).join('')
	// col –∫–∞–∂–¥—ã–π —Ä–∞–∑ –±—É–¥–µ—Ç –≤—ã–∑—ã–≤–∞—Ç—å—Å—è —Å –Ω–æ–≤—ã–º –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º

	return row(html)
	// –≤—ã–≤–æ–¥ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –º–∞—Å—Å–∏–≤–∞ –≤ —Å—Ç—Ä–æ–∫—É —Ä–∞–∑–¥–µ–ª—è–µ—Ç—Å—è –∑–∞–ø—è—Ç—ã–º–∏
	// .join('') —ç—Ç–æ —É—Å—Ç—Ä–∞–Ω—è–µ—Ç
}

// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –∫–∞—Ä—Ç–∏–Ω–∫–∏
function image(block) {
	return row(`<img src="${block.value}" alt="" />`)
}

// –æ–±—ä–µ–∫—Ç —Å —Ñ—É–Ω–∫—Ü–∏—è–º–∏ –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ html –±–ª–æ–∫–æ–≤
export const templates = {
	title,
	text,
	columns,
	image
}